import React from 'react'
import './style.css'
import img1 from '../../img/image 2.png'
import img2 from '../../img/Vector.png'

export default function Card() {
  return (
    <div className='Card'>
        <img src={img1} alt="" />
        <h3>Dunyoning ishlari</h3>
        <p>O’tkir Hoshimov</p>
        <div className='card__pupulation'>
            <img src={img2} alt="" />
            <span> 4.1 • 3400 ta fikrlar</span>
        </div>
    </div>
  )
}
