import React from 'react'
import logo from '../../img/logo.png'
import odam from '../../img/odam1.png'
import './style.css'


export default function Nav() {
  return (
    <div className='dad__nav'>
        <img src={logo} alt=""  className='animate__animated animate__rotateIn  animate__bounce logo '/>
        <nav className='nav'>
            <ul className='nav__list'>
                <li className='list-item bord animate__animated animate__backInDown' >Bosh sahifa</li>
                <li className='list-item bord animate__animated animate__backInDown' >Nasr</li>
                <li className='list-item bord animate__animated animate__backInDown' >Nazm</li>
                <li className='list-item bord animate__animated animate__backInDown' >Maqolalar</li>
                <li className='list-item bord animate__animated animate__backInDown' >Forum</li>
            </ul>
        </nav>
        <img src={odam} alt=""  className='odam animate__animated animate__backInDown'/>
    </div>
  )
}
